<template>
    <div>
        <a-upload v-if="ready"
                  name="file"
                  :multiple="true"
                  :action="this.$store.state.common.uploadUrl"
                  :headers="headers"
                  :defaultFileList="defaultFileList"
                  @preview="handlePreview"
                  @change="handleChange">
            <a-button v-if="!value||(value&&value.length < length)">
                <a-icon type="upload"/>
                点击上传
            </a-button>
        </a-upload>
        <div v-else>
            数据加载中。。
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            value: {
                required: false,
                type: [Array],
                default() {
                    return []
                }
            },
            length: {
                required: false,
                type: Number,
                default() {
                    return 1;
                }
            },
        },
        model: {
            prop: 'value',
            event: 'change'
        },
        data() {
            return {
                ready: false,
                headers: {
                    authorization: 'authorization-text',
                },
            }
        },
        computed: {
            defaultFileList() {
                if (!this.value) {
                    return [];
                }
                return this.value;
            }
        },
        methods: {
            handleChange(info) {
                if (info.file.status !== 'uploading') {
                }
                if (info.file.status === 'done' && info.file.response.success === 1) {
                    this.$message.success(`${info.file.name} file uploaded successfully`);
                } else if (info.file.status === 'error') {
                    this.$message.error(`${info.file.name} file upload failed.`);
                    return;
                } else {
                    // this.$message.error(`${info.file.name} 上传失败，请检查接口`);
                }
                this.updateList(info.fileList);
            },
            handlePreview(file) {
                let url = file.url ? this.$store.state.common.fileBaseUrl + file.url : null;
                url = url ? url : file.thumbUrl;
            },
            updateList(list) {
                let fileList = list;
                let temp = [];
                for (let i = 0; i < fileList.length; i++) {
                    if (fileList[i].hasOwnProperty('response') && fileList[i].response) {
                        fileList[i].url = fileList[i].response.data;
                    }
                    temp.push(fileList[i]);
                    temp[i].originFileObj = null;
                    temp[i].lastModifiedDate = null;
                }
                this.$emit('change', temp);
            },
        },
        mounted() {
            setTimeout(() => {
                this.ready = true;
            }, 3000)
        }
    }
</script>
