<template>
    <a-tree-select
            :style="styleValue"
            :treeData="treeData"
            :value="getValue(value)"
            treeCheckable
            @change="onChange"
            searchPlaceholder='请点击选择'
            treeNodeFilterProp='label'
    />
</template>

<script>
    import {TreeSelect} from 'ant-design-vue'
    import {getDepartmentUsersSelectTreeList} from '@/api/department'

    export default {
        props: {
            value: {
                required: false,
                default() {
                    return []
                }
            },
            styleValue: {
                required: false,
                default: () => {
                    return {'min-width': '200px'}
                }
            },
        },
        model: {
            prop: 'value',
            event: 'change'
        },
        data() {
            return {
                treeData: [],
                isReady: false,//treeData init&&initValue set
            }
        },
        methods: {
            getValue(value) {
                return value
            },
            init() {
                getDepartmentUsersSelectTreeList()
                    .then(res => {
                        this.treeData = res;
                    })
            },
            onChange(value) {
                this.$emit('change', value);
            },
        },
        created() {
            this.init();
        }
    }
</script>
