<template>
    <a-form @submit="handleSubmit"
            :autoFormCreate="(form)=>{this.form = form}">
        <a-form-item
                label='标题'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="comment_title"
        >
            <a-input/>
        </a-form-item>
        <a-form-item
                label='留言内容'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="comment_content"
        >
            <a-textarea :autosize="{ minRows: 2, maxRows: 6 }"/>
        </a-form-item>

        <a-form-item
                label='留言对象'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="to_user_ids"
        >
            <SelectUsers/>
        </a-form-item>
        <a-form-item
                label='是否发送短信通知'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                :fieldDecoratorOptions="{valuePropName:'checked'}"
                fieldDecoratorId="is_send_sms"
        >
            <a-switch/>
        </a-form-item>
        <a-form-item
                label='留言审核人'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="audit_user_id"
        >
            <SelectUser/>
        </a-form-item>
        <a-form-item
                label='留言时间'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="leave_at"
        >
            <DataTime/>
        </a-form-item>
        <a-form-item
                label='附件'
                :labelCol="{ span: 3 }"
                :wrapperCol="{ span: 15 }"
                fieldDecoratorId="more_files"
        >
            <upload-files :length="3"/>
        </a-form-item>
        <a-form-item
                :wrapperCol="{ span: 12, offset: 5 }"
        >
            <a-button type='primary' htmlType='submit'>
                提交
            </a-button>
        </a-form-item>
    </a-form>
</template>

<script>
    import {getComment, createComment, updateComment} from '@/api/comment'
    import DataTime from '@/components/select/DateTime'
    import SelectUser from '@/components/select/SelectUser'
    import SelectUsers from '@/components/select/SelectUsers'
    import uploadFiles from '@/components/upload/UploadFiles'

    export default {
        components: {
            DataTime, SelectUser, SelectUsers, uploadFiles
        },
        props: {
            id: {
                type: [Number],
                default() {
                    return 0
                },
            }
        },
        data() {
            return {
                log: console.log,
                form: {},
            }
        },
        computed: {
        },
        methods: {
            init() {
                if (!this.id) {
                    return;
                }

                getComment(this.id)
                    .then(res => {
                        this.form.setFieldsValue({
                            audit_user_id: res.data.user_id,
                            comment_content: res.data.comment_content,
                            comment_title: res.data.comment_title,
                            leave_at: res.data.leave_at,
                            to_user_ids: res.data.to_user_ids,
                            more_files: res.data.more.hasOwnProperty('files') ? res.data.more.files : null,
                        })
                    })
            },
            beforeSubmit() {
                return true;
                let res = false
                // this.form.validateFields(
                //     (err) => {
                //         if (!err) {
                //             res = true
                //         }
                //     })
                return res
            },
            handleSubmit(e) {
                e.preventDefault();

                let request;

                let data = this.form.getFieldsValue();

                if (this.id) {
                    request = updateComment(this.id, data)
                } else {
                    request = createComment(data)
                }
                request.then(() => {
                    let msg = this.id ? '更新成功' : '新增成功';
                    this.$message.success(msg);
                })
            },
        },
        mounted() {
            this.init();
        },
    }
</script>
